import React from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import homeIcon from './homeIcon3.png';
import questionMark from './questionMark2.png';
import noteIcon from './note3.png';

const NavButton = ({ isApp, isAbout }) => <Btn about={isAbout} app={isApp} />;

const Btn = styled.button`
  border: none;
  width: 5vw;
  height: 5vw;
  background-color: transparent;
  background-image: url(${homeIcon});
  background-size: 100%;
  margin-left: 2vw;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
  -moz-transform-origin: 50% 50% 0;
  cursor: pointer;
  outline: none;
  display: block;
  z-index: 3;

  :hover {
    transform: scale(1.25);
  }

  ${({ app }) =>
    app &&
    css`
      background-image: url(${noteIcon});
    `}
  ${({ about }) =>
    about &&
    css`
      background-image: url(${questionMark});
      background-size: cover;
    `}
`;

export default NavButton;

NavButton.propTypes = {
  isApp: PropTypes.bool,
  isAbout: PropTypes.bool,
};

NavButton.defaultProps = {
  isApp: false,
  isAbout: false,
};
