import React, { useEffect, useRef } from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import gsap from 'gsap';

const Header = ({ secondary, text }) => {
  const header = React.useRef(null);
  useEffect(() => {
    const h1 = header.current;
    const tl = gsap.timeline({ defaults: { delay: 4.5 } });

    gsap.set(h1, { autoAlpha: 0 });
    tl.to(h1, 0.6, { autoAlpha: 1 });
  });
  return (
    <H1 ref={header} secondary={secondary}>
      {text}
    </H1>
  );
};

const H1 = styled.h1`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
  margin: 0;
  text-align: center;
  font-size: 5vw;
  font-family: 'Montserrat', sans-serif;
  color: hsl(353, 100%, 36%);
  font-weight: 500;
  margin-top: 30vh;
  margin-left: 40vw;
  width: 50vw;

  ${({ secondary }) =>
    secondary &&
    css`
      font-size: 1.8rem;
      margin-top: 5vh;
    `}
`;

export default Header;

Header.propTypes = {
  secondary: PropTypes.bool,
  text: PropTypes.string,
};
Header.defaultProps = {
  secondary: false,
  text: 'example text',
};
