import React, { useEffect } from 'react';
import styled from 'styled-components';
import NavButton from '../../atoms/NavButton/NavButton';
import gsap from 'gsap';
import { NavLink } from 'react-router-dom';

const Navbar = () => {
  const nav = React.useRef(null);
  useEffect(() => {
    const tl = gsap.timeline({
      defaults: { ease: 'power3.inOut', delay: 5.5 },
    });
    const navbar = nav.current;

    gsap.set(navbar, { x: -125 });

    tl.to(navbar, 0.75, { x: 0 });
  });
  return (
    <Nav ref={nav}>
      <NavLink exact to="/swieta/">
        <NavButton as={NavLink} to="/swieta/" />
      </NavLink>
      <NavLink exact to="/swieta/app">
        <NavButton isApp />
      </NavLink>
      <NavLink exact to="/swieta/about">
        <NavButton isAbout />
      </NavLink>
    </Nav>
  );
};

const Nav = styled.div`
  width: 9vw;
  height: 100vh;
  background-color: hsl(353, 100%, 36%);
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
  align-content: center;
  margin: none;
  position: fixed;
`;

export default Navbar;
