import React, { useEffect } from 'react';
import styled from 'styled-components';
import gsap from 'gsap';

const About = () => {
  const wrapper = React.useRef(null);

  useEffect(() => {
    const tl = gsap.timeline();
    const header = wrapper.current.children[0];
    const text = wrapper.current.children[1];

    gsap.set([header, text], { autoAlpha: 0, x: -125 });

    tl.to(header, 0.6, { autoAlpha: 1, x: 0 }).to(text, 0.7, {
      autoAlpha: 1,
      x: 0,
    });
  });
  return (
    <Div ref={wrapper}>
      <H1>Projekt</H1>
      <P>
        Aplikacja została stworzona przez Krystiana Zieję z wykorzystaniem
        frameworka React oraz kilku pomniejszych bibliotek
      </P>
    </Div>
  );
};

export default About;

const H1 = styled.h1`
  margin: 0;
  text-align: center;
  transform: translateY(10vh);
  font-size: 4vw;
  @import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
  font-family: 'Montserrat', sans-serif;
`;
const Div = styled.div`
  width: 91vw;
  margin-left: 9vw;
`;
const P = styled.p`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
  font-family: 'Montserrat', sans-serif;
  font-size: 3vw;
  text-align: center;
  transform: translateY(15vh);
  max-width: 50vw;
  margin-left: 20.5vw;
`;
