import React from 'react';
import styled from 'styled-components';

class TitleInput extends React.Component {
  constructor(props) {
    super(props);
    this.inputRef = React.createRef();
  }
  state = {
    focus: false,
    translateY: true,
    fontBig: true,
  };
  focusFunc = async () => {
    await this.setState({ focus: true });
  };
  blurFunc = async (e) => {
    if (e.target.value === null || e.target.value === '') {
      await this.setState({ focus: false });
    }
  };
  Load = (e) => {
    e.target.value = this.props.value;
  };
  componentDidMount = () => {
    if (this.inputRef.value === null || this.inputRef.value === '') {
      this.setState({ focus: false });
    }
    if (this.inputRef.value !== null || this.inputRef.value !== '') {
      this.setState({ focus: true });
    }
  };
  render() {
    return (
      <Div>
        <Label focus={this.state.focus}>Tytuł kartki</Label>
        <Input
          ref={this.inputRef}
          focus={this.state.focus}
          onFocus={this.focusFunc}
          onBlur={this.blurFunc}
        >
          {this.props.value}
        </Input>
      </Div>
    );
  }
}

const Input = styled.textarea`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
  border: none;
  background-color: transparent;
  border-bottom: solid 0.1rem black;
  outline: none;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.5rem;
  border-color: ${(props) =>
    props.focus === true ? 'hsl(353, 100%, 36%)' : 'black'};
  width: 30vw;
  justify-self: center;
  resize: none;
  height: 4vh;
  overflow: hidden;
`;

const Div = styled.div`
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  margin-top: 3vh;
`;

const Label = styled.label`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
  transform: ${({ focus }) =>
    focus === false ? 'translateY(100%)' : 'translateY(10%)'};
  font-size: ${({ focus }) => (focus === false ? '1.4rem' : '1rem')};
  z-index: -1;
  transition: 0.3s;
  font-family: 'Montserrat', sans-serif;
  color: ${(props) => (props.focus === true ? 'hsl(353, 100%, 36%)' : 'black')};
  margin-right: 23.5vw;
`;

export default TitleInput;
