import React from 'react';
import AddingForm from '../../molecules/AddingForm/AddingForm';
import Kartka from '../../atoms/Kartka/Kartka';
import { exportComponentAsJPEG } from 'react-component-export-image';

import styled, { css } from 'styled-components';

class AppPanel extends React.Component {
  constructor(props) {
    super(props);
    this.componentRef = React.createRef();
  }
  state = {
    title: 'Siema',
    text: 'Wesołych Świąt',
    fontColorH: 0,
    fontColorS: '0%',
    fontColorL: '50%',
    fs: 100,
    fl: 0,
    form: false,
    imageSrc: null,
  };
  show = async () => {
    await this.setState({ form: !this.state.form });
  };
  hide = async (e) => {
    e.preventDefault();
    await this.setState({ form: false });
  };
  setValue = (e) => {
    e.preventDefault();
    this.setState({ title: e.target[0].value });
    this.setState({ text: e.target[1].value });
    this.setState({ fontColorH: e.target[2].value });
    this.setState({ fontColorS: `${e.target[3].value}%` });
    this.setState({ fontColorL: `${e.target[4].value}%` });
    this.show();
  };
  read = (e) => {
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        this.setState({ imageSrc: reader.result });
      }
    };
    reader.readAsDataURL(e.target.files[0]);
    console.log(e.target.files[0]);
  };

  render() {
    const ref = this.componentRef;
    return (
      <>
        <Button
          secondary
          onClick={() =>
            exportComponentAsJPEG(ref, {
              fileName: 'KartkaŚwiąteczna',
              html2CanvasOptions: {
                scale: 2,
              },
            })
          }
        >
          Pobierz Kartkę
        </Button>
        <Button sec={true} onClick={this.show}>
          Customize
        </Button>
        {this.state.form && (
          <AddingForm
            h={this.state.fontColorH}
            s={this.state.fontColorS}
            l={this.state.fontColorL}
            title={this.state.title}
            text={this.state.text}
            setFunc={this.setValue}
            hideFunc={this.show}
            imageFunc={this.read}
          />
        )}
        <Div ref={this.componentRef}>
          <Kartka
            h={this.state.fontColorH}
            s={this.state.fontColorS}
            l={this.state.fontColorL}
            title={this.state.title}
            text={this.state.text}
            background={this.state.imageSrc}
          />
        </Div>
      </>
    );
  }
}

export default AppPanel;

const Div = styled.div`
  width: 124vh;
  height: 84vh;
  margin-left: 21.25vw;
  transform: translateY(8vh);
  display: block;
`;

const Button = styled.button`
  width: 11vw;
  height: 5vh;
  color: white;
  background-color: hsl(353, 100%, 36%);
  @import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
  font-family: 'Montserrat', sans-serif;
  border: none;
  font-size: 1.1rem;
  margin-left: 2vw;
  transition: background-color 0.6s;
  will-change: background-color;
  cursor: pointer;
  transform: translateY(1vh);
  border-radius: 50px;
  outline: none;

  :hover {
    background-color: hsl(353, 100%, 44%);
  }

  ${(props) =>
    props.secondary &&
    css`
      margin-left: 0;
      margin-right: 2vw;
    `}
`;
