import React from 'react';
import styled from 'styled-components';

const Kartka = (props) => {
  const color = `hsl(${props.h},${props.s},${props.l})`;
  return (
    <Div image={props.background} fontColor={color}>
      <h1>{props.title}</h1>
      <p>{props.text}</p>
    </Div>
  );
};

const Div = styled.div`
  width: 124vh;
  height: 84vh;
  border: solid black 1px;
  display: block;
  h1 {
    font-size: 3.25vw;
  }
  p {
    max-width: 100vh;
    margin-left: 12vh;
    font-size: 1.5vw;
  }
  background-image: ${({ image }) => `url(${image})`};
  background-size: cover;
  color: ${(props) => props.fontColor};
`;

export default Kartka;
