import React from 'react';
import styled, { css } from 'styled-components';

const AcceptButton = ({ sec }) => <Abtn secondary={sec}>Akceptuj</Abtn>;

const Abtn = styled.button`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
  border: solid hsl(353, 100%, 36%) 0.2vh;
  border-radius: 50px;
  color: white;
  background-color: hsl(353, 100%, 36%);
  width: 16vw;
  height: 10vh;
  font-size: 1.25rem;
  font-family: 'Montserrat', sans-serif;
  transition: 0.6s;
  outline: none;
  cursor: pointer;
  justify-self: center;
  margin-top: 3vh;

  :hover {
    background-color: hsl(353, 100%, 45%);
  }

  ${({ secondary }) =>
    secondary &&
    css`
      width: 8vw;
      height: 5vh;
      font-size: 1.1rem;
    `}
`;

export default AcceptButton;
