import React from 'react';
import styled from 'styled-components';
import TitleInput from '../../atoms/TitleInput/TitleInput';
import TextField from '../../atoms/TextField/TextField';
import AcceptButton from '../../atoms/AcceptButton/AcceptButton';
import InputText from '../../atoms/InputText/InputText';
import AddingFormHeader from '../../atoms/AddingFormHeader/AddingFormHeader';

const AddingForm = (props) => {
  const myForm = React.useRef(null);
  const [color1, setColor1] = React.useState();
  const [color2, setColor2] = React.useState();
  const [color3, setColor3] = React.useState();
  const color = `hsl(${color1 || 0},${color2 || '0%'},${color3 || '50%'})`;
  return (
    <Form onSubmit={props.setFunc} ref={myForm}>
      <AddingFormHeader />
      <TitleInput value={props.title} />
      <InputText />
      <TextField value={props.text} />
      <input
        onChange={(e) => {
          setColor1(e.target.value);
        }}
        type="range"
        min="0"
        max="360"
      />
      <input
        onChange={(e) => {
          setColor2(`${e.target.value}%`);
        }}
        type="range"
        min="0"
        max="100"
      />
      <input
        onChange={(e) => {
          setColor3(`${e.target.value}%`);
        }}
        type="range"
        min="0"
        max="100"
      />
      <Square fontColor={color} />
      <input onChange={props.imageFunc} accept="image/*" type="file" />
      <AcceptButton sec={false} type="submit" />
    </Form>
  );
};

const Form = styled.form`
  width: 40vw;
  height: 90vh;
  position: fixed;
  z-index: 1000;
  margin-left: 30vw;
  margin-top: 5vh;
  background-color: white;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.09), 0 4px 2px rgba(0, 0, 0, 0.09),
    0 8px 4px rgba(0, 0, 0, 0.09), 0 16px 8px rgba(0, 0, 0, 0.09),
    0 32px 16px rgba(0, 0, 0, 0.09);
`;

export default AddingForm;

const Square = styled.div`
  width: 3vw;
  height: 1vw;
  border: solid black 1px;
  align-self: center;
  margin-left: 18.5vw;
  z-index: 9999;
  background-color: ${(props) => props.fontColor};
`;
